import { axios } from '@/utils/http';
import HOST from '@/config/host';
// parentCode
export function getOrgData(parentCode = '10000'){
  return axios.get(HOST + '/org_chart/getDepartment', {
    params: {
      parentCode
    }
  });
}
// 
export function addDepartment(options){
  return axios.post(HOST + '/org_chart/addDepartment', options);
}
//
export function removeDepartment(options){
  return axios.post(`${HOST}/org_chart/delDepartment`,options);
}